.navbar-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white;
  width: 100%;
  justify-content: space-between;
  margin: 0px;
  padding: 0px;
  height: 100%;
  margin: 0 2rem;
}
.logo {
  flex: 1;
}
.controls {
  display: flex;
  flex: 3;
  justify-content: right;
  align-items: center;
  margin: 0px;
  padding: 0px;
  height: inherit;
  /* margin-right: 2rem; */
}
.market-button {
  display: flex;
  cursor: pointer;
  height: 100%;
  font-weight: 400;
  align-items: center;
  border-left: grey 1px solid;
  padding-left: 1rem;
}

.market-button .marketicon {
  height: 2rem;
}

.profile-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
  /* width: fit-content; */
  border-left: grey 1px solid;
  position: relative;
}
.profile-container .profile-dd-list {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  /* left: 0; */
  right: 0;
  top: 70px;
}
.profile-container .profile-dd-list .dd-item {
  width: 150px;
  border-color: transparent;
  background-color: #14181d;
  color: white;
  border-bottom: 1px grey solid;
  padding: 0.5rem 1rem;
}

.profile-container .profile-dd-list .dd-item:hover {
  background-color: #383838;
}

.disable {
  display: none;
}

.profile-container .user-logo {
  display: flex;
  /* position: relative; */
  align-items: center;
  height: 90%;
  width: fit-content;
  padding: 0.1rem 0.7rem;
  cursor: pointer;
  background-color: transparent;
  border-color: transparent;
  margin: 0;
}
.profile-container .user-logo .logo-icon {
  display: flex;
  align-items: center;
}
.profile-container .user-logo .logo-icon .logo-img {
  border-radius: 50%;
  border: #00b0ff 3px solid;
  width: 3rem;
  height: 3rem;
}

.connect-wallet {
  display: flex;
  align-items: center;
  border-left: grey 1px solid;
  padding: 0 0.7rem;
  cursor: pointer;
  height: 100%;
}
.connect-wallet .wallet-icon {
  display: flex;
  width: 100%;
}

.balance-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  padding: 0 0.5rem;
  border-left: grey 1px solid;
}
.balance-container .balance-title {
  display: flex;
  color: grey;
  font-size: 0.7rem;
  align-self: center;
}
.balance-container .balance-amount {
  display: flex;
  flex-direction: row;
  /* height: 100%; */
}
.balance-container .balance-amount .balance-eth {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
}
.balance-container .balance-amount .balance-eth .eth-icon {
  display: flex;

  height: 70%;
}

.balance-container .balance-amount .balance-sroot {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 0.5rem;
}

.balance-container .balance-amount .balance-sroot .sroot-icon {
  display: flex;
  height: 70%;
  padding-right: 0.3rem;
}

.signin {
  display: flex;
  align-items: center;
  border-left: 1px grey solid;
  height: 100%;
  padding: 0 2rem;
}
.signin .sign-button {
  border-radius: 20px;
  border-color: transparent;
  background-color: #0084ff;
  color: white;
  padding: 3px 20px;
  cursor: pointer;
  /* margin-right: 20px; */
}
