.mynfts {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 20px;
}
.mynfts .mynfts-title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  color: grey;
  font-size: 1.5rem;
  font-weight: bold;
}

.mynfts .mynfts-body {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.mynfts .mynfts-body .mynfts-nft-detail {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.mynfts .mynfts-body .mynfts-nft-detail .mynfts-nft-title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: grey;
  font-weight: bold;
  margin-top: 2rem;
}

.mynfts .mynfts-body .mynfts-nft-detail .mynfts-nft-body {
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: row; */
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 2rem;
}
