.mylands {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 20px;
}

.mylands .mylands-title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  font-weight: bold;
  color: grey;
  font-size: 1.5rem;
}

.mylands .mylands-lands {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 540px;
}

.mylands .mylands-lands .mylands-lands-unity {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 100%;
}
.mylands .mylands-lands .mylands-lands-panel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 20%;
  height: 100%;
  padding: 20px;
}

.flex {
  display: flex;
}
.column {
  flex-direction: column;
}
.row {
  flex-direction: row;
}
.justify-start {
  justify-content: flex-start;
}
.justify-center {
  justify-content: center;
}
.justify-between {
  justify-content: space-between;
}

.w100 {
  width: 100%;
}
.bold {
  font-weight: bold;
}

.grey {
  color: grey;
}

.mylands .mylands-lands .mylands-lands-panel .map {
  border-radius: 20px;
  height: fit-content;
  border: 2px grey solid;
}

.mylands .mylands-lands .mylands-lands-panel .buy-button {
  border-color: transparent;
  padding: 5px;
  border-radius: 20px;
  background-color: #00b0ff;
  color: white;
}

.mylands .mylands-lands .mylands-lands-panel .buy-button:hover {
  background-color: rgb(153, 206, 250);
}

.ribbon-wrapper {
  width: 85px;
  height: 88px;
  overflow: hidden;
  position: absolute;
  top: -3px;
  left: -3px;
}

.ribbon {
  background-color: #ff3806;
  font: bold 15px sans-serif;
  color: #333;
  text-align: center;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  position: relative;
  padding: 7px 0;
  top: 15px;
  left: -30px;
  width: 120px;
  color: #fff;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
