.mytokens {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 20px;
}

.mytokens .title {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  width: 100%;
  font-weight: bold;
  color: grey;
  font-size: 1.5rem;
}

.mytokens .token-detail {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}
.mytokens .token-detail .token-detail-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  justify-content: center;
  color: grey;
  font-weight: bold;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.mytokens .token-detail .token-detail-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 90%;
  justify-content: space-around;
}
.mytokens .token-detail .token-detail-info .title {
  font-size: 1rem;
}
.mytokens .token-detail .token-detail-info .token-detail-info-item {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.mytokens .token-detail .token-detail-info .token-detail-info-item .title {
  color: grey;
}

.mytokens .token-detail .token-detail-info .token-detail-info-item .value {
  font-weight: bold;
}

.token-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 90%;
}
.token-buttons button[type='button'] {
  width: 100%;
  border-color: transparent;
  padding: 5px;
  background-color: grey;
  color: white;
}

.mytokens .token-detail .token-stake-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
}
.mytokens .token-detail .token-stake-section .staking-area {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}

.mytokens .token-detail .token-stake-section .staking-area .staking-value input[type='number'] {
  padding: 5px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 80%;
  background: #f1f1f1;
}

.mytokens .token-detail .token-stake-section .staking-area .staking-value button {
  float: left;
  width: 20%;
  padding: 5px;
  background: grey;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none;
  cursor: pointer;
}

.mytokens .token-detail .token-stake-section .staking-area .staking-approve .button-approve {
  border-color: transparent;
  border-radius: 20px;
  background-color: grey;
  color: white;
  padding: 3px 30px;
  width: 250px;
}

.mytokens .token-detail .token-stake-section .unstaking-area {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.mytokens .token-detail .token-stake-section .unstaking-area .unstaking {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.mytokens .token-detail .token-stake-section .unstaking-area .unstaking .unstaking-value input[type='number'] {
  padding: 5px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 80%;
  background: #f1f1f1;
}

.mytokens .token-detail .token-stake-section .unstaking-area .unstaking .unstaking-value button {
  float: left;
  width: 20%;
  padding: 5px;
  background: grey;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none;
  cursor: pointer;
}

.mytokens .token-detail .token-stake-section .unstaking-area .unstaking .unstaking-approve .button-approve {
  border-color: transparent;
  border-radius: 20px;
  background-color: grey;
  color: white;
  padding: 3px 30px;
  width: 250px;
}

.mytokens .token-detail .token-stake-section .unstaking-area .rewarding {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 0.5rem;
}
.mytokens .token-detail .token-stake-section .unstaking-area .rewarding .rewarding-value input[type='number'] {
  padding: 5px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 80%;
  background: #f1f1f1;
}
.mytokens .token-detail .token-stake-section .unstaking-area .rewarding .rewarding-value button {
  float: left;
  width: 20%;
  padding: 5px;
  background: grey;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none;
  cursor: pointer;
}

.mytokens .token-detail .token-stake-section .unstaking-area .rewarding .rewarding-approve .button-approve {
  border-color: transparent;
  border-radius: 20px;
  background-color: grey;
  color: white;
  width: 250px;
  padding: 3px 30px;
}

.mytokens .token-detail .farms-stake-status {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 60%;
  margin-bottom: 20px;
}

.mytokens .token-detail .farms-stake-status .status {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 20px;

  color: grey;
  font-weight: bold;
}

.mytokens .token-detail .farms-stake-status .divider {
  border-bottom: 2px grey solid;
  width: 100%;
  margin-top: 1rem;
}
.mytokens .token-detail .farms-stake-status .burn-rootx {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  width: 100%;
  margin-top: 1.5rem;
}
.mytokens .token-detail .farms-stake-status .burn-rootx button {
  width: 100%;
  border-color: transparent;
  background-color: grey;
  color: white;
}

.mytokens .token-detail .farms-stake-status .burn-rootx button:hover {
  background-color: rgb(185, 185, 185);
}

.react-tabs__tab-list {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: none;
}
.react-tabs {
  width: 100%;
  height: 100%;
}

.react-tabs__tab--selected {
  background: #fff;
  background-color: ghostwhite;
  color: grey;
  border-radius: 5px 5px 0 0;
  border-bottom: 2px grey solid;
  font-weight: bold;
}

.react-tabs__tab {
  display: inline-block;
  /* border: 1px solid transparent; */
  border-top: 1px solid transparent;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  /* border: none; */
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 6px 12px;
  cursor: pointer;
  /* color: grey; */
}

.react-tabs__tab-panel {
  margin-top: 1rem;
}

.token-buttons .left-button {
  margin-right: 10px;
}

.token-buttons .right-button {
  margin-left: 10px;
}

.token-buttons button[type='button']:hover {
  background-color: rgb(182, 182, 182);
}
.t-item {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
