.home-header {
  display: flex;
  width: 100%;
  height: 500px;
  position: relative;
  background: url(assets/imgs/bg.png) repeat-x;
}

.home-header .play-banner {
  position: absolute;
  top: 0;
  left: 0;
  /* background-color: rgba(0, 0, 0, 55%); */
  border-radius: 10px;
  width: 100%;
  height: 100%;
  padding: 50px 200px;
}

.home-header .play-banner .box-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  width: 500px;
  height: 100%;
}
.home-header .play-banner .box-container .box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  border-radius: 30px;
  background-color: rgba(0, 0, 0, 80%);
  padding: 40px;
}

.home-header .play-banner .box-container .box .ribbon {
  height: 60px;
  position: relative;
  left: 24px;
  top: 0;
}

.home-header .play-banner .box-container .box .ribbon {
  z-index: 0;
}

.home-header .play-banner .box-container .box .ribbon::before {
  content: '';
  background: url(https://www.sandbox.game/img/14_Home/Alpha_ribbon.png);
  background-repeat: no-repeat;
  background-size: contain;
  filter: brightness(0.85);
  width: 120%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: unset;
  left: unset;
  z-index: -1;
}

.home-header .play-banner .box-container .box .ribbon p {
  z-index: 2;
  color: white;
  font-size: 1.5rem;
}

.home-header .play-banner .box-container .box .title-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.home-header .play-banner .box-container .box .title-container h3 {
  font-size: 18px;
  line-height: 18px;
  font-family: 'Montserrat SemiBold';
  color: #00b0ff;
  text-align: left;
  text-transform: uppercase;
  margin-bottom: 11px;
}

.home-header .play-banner .box-container .box .title-container .title {
  font-size: 55px;
  line-height: 45px;
  font-family: 'Montserrat Bold';
  color: #fff;
  text-align: left;
}

.home-header .play-banner .box-container .box .title-container .date-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.home-header .play-banner .box-container .box .title-container .date-container {
  color: white;
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.home-header .play-banner .box-container .box .title-container .date-container .maintitle {
  color: #00b0ff;
}

.home-header .play-banner .box-container .box .title-container .date-container .subtitle {
  font-size: 20px;
  margin-left: 30px;
}

.home-header .play-banner .box-container .box .title-container .btns {
  display: flex;
  flex-direction: row;
}

.home-header .play-banner .box-container .box .title-container .btns .discover-button {
  border-radius: 10px;
  border-color: transparent;
  background-color: #0084ff;
  color: white;
  padding: 5px 20px;
  cursor: pointer;
  margin-right: 20px;
}

.home-header .play-banner .box-container .box .title-container .btns .buy-button {
  border-radius: 10px;
  border: 2px #0084ff solid;
  background-color: transparent;
  color: white;
  padding: 5px 20px;
  cursor: pointer;
}

.home-header .play-banner .box-container .box .title-container .btns .buy-button:hover {
  background-color: #05f;
}

.home-header .play-banner .box-container .box .title-container .btns .discover-button:hover {
  background-color: #05f;
}
