::-webkit-scrollbar {
  width: 5px !important;
  background-color: #000;
}

::-webkit-scrollbar-thumb {
  background-color: #ccc;
}

* {
  padding: 0;
  margin: 0;
  font-family: 'Kanit', sans-serif;
}

a {
  text-decoration: none !important;
}

#root {
  margin: auto;
}

body {
  background-color: #0d253b;
  overscroll-behavior: none;
  overflow: hidden;
}
