.side-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  width: 80%;
  margin: 1rem 0 0 0;
}

.side-button .button-title {
  color: rgb(139, 138, 138);
  font-weight: 700;
  font-size: 12px;
}
.side-button .button-img {
  width: 50%;
}
.clicked {
  background-color: rgb(168, 168, 168, 100);
  border-radius: 5px;
}
