.play-area {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}
.play-area .play-title {
  display: flex;
  flex-direction: row;
  width: 100%;
  color: white;
  align-items: center;
  justify-content: center;
}
.play-area .unity-area {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 100px;
  border-radius: 20px;
  width: 100%;
}
